// 新闻快报页面

<template>
  <div class="news pt">
    <DropdownBar />
    <BannerLogo title="新闻快报" :picUrl="bannerurl" />
    <ContentList v-for="(item, index) in contentListData" :key="index" :initData="item" />
    <FooterBar />
  </div>
</template>

<script>
import DropdownBar from "@/components/DropdownBar";
import FooterBar from "@/components/FooterBar";
import ContentList from "@/components/ContentList";
import BannerLogo from "@/components/BannerLogo";
import Api from "../../api/index.js";
export default {
  name: "news",
  data() {
    return {
      bannerurl: require("../../assets/image/new_logo.png"),
      // 列表数据
      contentListData: [
        // {
        //   type: 0,
        //   url: "https://www.baidu.com/img/bd_logo1.png?where=super",
        //   describe: "百度图片",
        //   title: "文章标题 111111",
        //   content: "文章内容 1111111",
        //   time: "2018.09.14",
        //   id: 1
        // }
      ]
    };
  },
  created() {
    this.initPage();
  },
  methods: {
    // 初始化渲染页面数据
    initPage() {
      Api.getNewsList().then(res => {
        let result = res.data;
        if (result.msg_type == 0) {
          this.contentListData = JSON.parse(JSON.stringify(result.data.list));
        }
      });
    }
  },
  components: {
    DropdownBar,
    FooterBar,
    ContentList,
    BannerLogo
  }
};
</script>
<style scoped lang="less">
.news {
  background-color: #f2f2f2;
}
</style>
